*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
p,
figure,
picture {
  margin: 0;
}

body {
  line-height: 1.5;
  min-height: 100vh;
  background-color: black;
}

img,
picture {
  max-width: 100%;
  display: block;
}

.Mui-selected {
  color: white;
}
.css-1ee5err-MuiButtonBase-root-MuiBottomNavigationAction-root.Mui-selected {
  color: white !important;
}
.MuiBottomNavigationAction-label {
  color: white;
}
.MuiSvgIcon-root {
}
.show {
  opacity: 1;
}
.hover-card {
  opacity: 0;
}
.image {
  background-size: cover;
  object-fit: cover;
  background-position: center;
  width: 100%;
}
.fade {
  animation: Fade 700ms ease-in-out;
}
.active {
  transform: scale(1.2) translateY(-10%);
}
.scrollbar::-webkit-scrollbar {
  width: 5px;
}
.scrollbar::-webkit-scrollbar-thumb {
  background-color: #d1ba7a;
}
.scrollbar::-webkit-scrollbar-track {
  background-color: black;
}
.black-scrollbar::-webkit-scrollbar {
  width: 8px;
}
.black-scrollbar::-webkit-scrollbar-thumb {
  background-color: black;
}
.black-scrollbar::-webkit-scrollbar-track {
  background-color: #d1ba7a;
}
.css-1160xiw-MuiPaper-root-MuiDrawer-paper {
  background-color: #d1ba7a !important;
}
.css-2vckkb-MuiSvgIcon-root {
  color: black;
}
@media screen and (min-width: 600px) {
  .css-2ihqhj-MuiGrid-root {
    max-width: 100% !important;
  }
}
@keyframes Fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes Slider {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
